ul#header {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: end;
  width: 100%;
  padding: 10px;
  height: 50px;
  z-index: 100;
}
ul#header li {
  margin-right: 10px;
  color: #fff;
  font-weight: bold;
}
ul#header li:nth-of-type(1) {
  font-size: var(--fontSizeThird);
  font-style: italic;
}